import * as React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"

import Layout from "../components/layout"
import Seo from "../components/seo"

import { Box, TextInput, Button } from 'grommet';
import { Add } from 'grommet-icons';


const IndexPage = () => {

  const [enteredAddress, setEnteredAddress] = React.useState(null)
  const [addresses, setAddresses] = React.useState([])

  const addAddress = (newAddress) => {
    setAddresses([ ...addresses, newAddress ])
  }

  const setAndClearAddress = () => {
    addAddress(enteredAddress) 
    setEnteredAddress(null)
  }

  return (
    <Layout>
      <Seo title="Home" />
      <Box>
        <h1>Add eth address</h1>
        <Box direction="row" gap='small'>
          <TextInput
            placeholder="Enter address..."
            value={enteredAddress}
            onChange={event => setEnteredAddress(event.target.value)}
          />
          <Button 
            primary
            icon={<Add size='small' />}
            size='small' 
            color='brand' 
            onClick={(e) => setAndClearAddress()} 
            label="Add" 
          />
        </Box>
      </Box>
      
    </Layout>
  )
}

export default IndexPage
